.search-area{
    background-color: rgb(14, 14, 14);
    display:grid;
    grid-template-rows:10vh;
    height:87vh;
    width:100%;
    overflow-y: scroll;
    overflow-x: hidden;
}
.search-area::-webkit-scrollbar {
    display: none;
  }
#search-bar{
    color:black;
    height:2.5rem;
    width:20rem;
    border-radius: 20px;
    margin:20px 0 0 15px;
    padding:0 0 0 10px;
}

.search button{
    height:2.5rem;
    width:2rem;
    cursor: pointer;
    position:relative;
    margin-right:-30px;
    margin-top:-80px;
    background-color:rgb(14, 14, 14);
    border:0;
}
.search-box{
    height:10vh;width:100%;
    display:grid;
   
    grid-template-columns: 0.3fr 4fr 3fr 3fr ;
    grid-gap:20px;
    color:white;
    align-items:center;
    margin:0 10px;
    font-size: 0.8rem;
}
.search-box img{
    width:100%;
}
.search-box button{
    width:100%;
}
.search-box a{
    width:100%;
    display:block;
}
.search-box:hover{
    background-color:black;
    color:rgba(255, 0, 0, 0.678);
    cursor:pointer;
}
