@media screen and (max-width:500px){
    #search-bar{
    
        height:2.5rem;
        width:20rem;
        border-radius: 20px;
        margin:20px 0 0 0;
        padding:0 0 0 10px;
    }
    .search-box:hover{
        background-color:black;
        color:rgba(255, 0, 0, 0.678);
        cursor:pointer
    }
    .search-box{
        height:60px ;width:100%;
        display:grid;
        grid-template-columns: 0.8fr 3.4fr 2.3fr 3.2fr ;
        grid-gap:20px;

        align-items:center;
        font-size: 0.4rem;
    }
 
    .sectionheader-search{
        display:grid; 
        grid-template-columns: 2fr 3.5fr 3.5fr 2fr 1.5fr;
        width:100%;
        background-color: black;
        color:white;
        align-items:end;
        font-size: 0.8rem;
    }
}


