
@import url('https://fonts.googleapis.com/css2?family=Pattaya&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pattaya&family=Suez+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pattaya&family=Suez+One&family=Varela+Round&display=swap');
.mainplayer{
    width:100%;
    height:87vh;
    background-color: #121212;
    grid-gap:10px;
    overflow-y:scroll;
    overflow-x: hidden;
    direction: ltr;
    /* scrollbar-width: none; */
}
.mainplayer::-webkit-scrollbar,.arrange::-webkit-scrollbar,.arrange1::-webkit-scrollbar {
    display: none;
  }
.banner{
    background-image:url('concert-.jpg');
    /* background-position-x:25px; */
    background-position-y:-305px;
    background-repeat: no-repeat;
    background-size: cover;
    margin:0 5px;
    opacity:2;
    height:45%;
  position:relative;
}

.banner h1{
    margin:3% 0 0 7%;
    font-size: 3.5rem;
    font-family: 'Varela Round', sans-serif;
    color:white;
    font-size: 3rem;
    position:absolute;
    z-index:4;
}
.banner span{
    color:rgba(255, 0, 0, 0.678);
    font-family: 'Varela Round', sans-serif;
}
.banner-overlay{
    height:100%;
    width:100%;
    position:absolute;
    top:0;
    z-index:3;
    background-color: black;
    opacity:0.7;
}
.arrange{
    display:grid;
    grid-template-columns:repeat(10,35vh);
    grid-gap:20px;
    /* background-color: black; */
    margin:10px 5vh 10px 5vh;
    overflow-x:scroll;
    height:35vh;
   
    /* overflow-x: scroll; */
}
.box{
    /* margin:10px 8px 20px 8px; */
    width:33vh;
    height:34vh; 
    -moz-box-shadow: 0 0 5px white;
    -webkit-box-shadow: 0 0 5px white;
    box-shadow: 0 5px 5px white;
}

.box:hover{
    background-color: black;
    opacity:1.2;
}

.box img{
    width:30vh;
    height:26vh;
    margin-top:4%;
    margin-left:5%;
    opacity:1.5; 
}
.box a {
    text-decoration: none;
    color:white;

}
.title{width:5fr;
color:white;
background-color: black;
height:10vh;
margin: 20px 10px 10px 7px;
text-align:center;
font-size:8vh;
padding:10px;
}

.box h4{
    text-align: center;
    font-size: larger;
}
