.library-middle{

    display:grid;
    overflow-y: hidden;
    width:100%;
    height:87vh;
    background-color: #121212;
    display:grid;
    grid-template-columns:repeat(3,1fr);
   grid-column-gap:60px;
}

.library-middle::-webkit-scrollbar {
    display: none;
  }

.link-to-liked-songs{
    height:40vh;
    background-color: #7c0000;
    background-image: linear-gradient(326deg, #7c0000 0%, #3e0000 74%);
    width:100%;
    border-radius:25px;
    margin:0 40%;
    opacity:0.8;
    position:relative;
    top:15%;
}
.link-to-liked-songs h1,.link-to-playlists h1{
    font-size:3rem;
    font-weight: 900;
    position:relative;
    top:25%;
    color:white;
    text-align:center;
    display: block;
   margin:20%
}
.link-to-liked-songs:hover,.link-to-playlists:hover{
    opacity:1;
}
.link-to-playlists{
    height:40vh;
    position:relative;
    top:15%;
    background-color: #130f40;
    background-image: linear-gradient(315deg, #130f40 0%, #000000 74%);
    width:100%;
    border-radius:25px;
    margin:0 40%;
    opacity:0.8;
}

.middle-library a{
   text-decoration: none;
}
.library-title{
    position:absolute;
    top:5vh;
   right:35%;
    font-size:3rem;
    color:white;

}

@media screen and (max-width:500px){
    .library-middle{

        overflow-x: hidden;
        width:100%;
        height:87vh;
        background-color: #121212;
    }
    
    .library-title{
        position:absolute;
        top:2vh;
      left:20%;
        font-size:2rem;
        color:white;
     width:80vw;
    }
    .link-to-liked-songs{
        height:30vh;
        width:80vw;
        background-color: #7c0000;
        background-image: linear-gradient(326deg, #7c0000 0%, #3e0000 74%);
       
        border-radius:25px;
        margin:0 10%;
        opacity:0.8;
        position:relative;
        top:25%;
    }
    .link-to-playlists{
        height:30vh;
        width:80vw;
        position:absolute;
        top:58%;
    left:-30%;
        background-color: #130f40;
        background-image: linear-gradient(315deg, #130f40 0%, #000000 74%);
       
        border-radius:25px;
     
        opacity:0.8;
    }

    .link-to-liked-songs h1,.link-to-playlists h1{
        font-size:2rem;
        font-weight: 900;
        position:absolute;
        top:10%;
        color:white;
        text-align:center;
        display: block;
      
    }
}