.my-playlists-middle{
    width:100%;
    height:87vh;
    background-color: #121212;
    display:grid;
    grid-template-columns:30vh;
    grid-template-columns:repeat(4,1fr);
    overflow-y: scroll;
} 
.my-playlists-middle button{
    background-color:black;
    color:white;
    height:3rem;
    display:block;
    width:12rem;
}

.my-playlists-middle::-webkit-scrollbar {
    display: none;
  }

.my-playlist-box-container{
    margin:30px ;
}
.my-playlist-box{
    height:30vh;
    width:30vh;
    background-color: black;
    color:white;
    margin:30px 30px;
    color:white;
}

.my-playlist-box button{
    height:30vh;
    width:30vh;
    background-color: #121212;
    color:white;
    margin:30px 30px;
    color:white;
}
.my-playlist-box a{
    text-decoration:none;
    color:white;
}

.delete-my-playlist:hover{
cursor: pointer;
}
@media screen  and (max-width:500px){
    .my-playlist-box{
        height:10vh;
        width:10vh;
        background-color: black;
        color:white;
        margin:30px 30px;
        color:white;
    }
    .my-playlists-middle{
        width:100%;
        height:87vh;
        background-color: #121212;
        display:grid;
        grid-template-columns:30vh;
        grid-template-rows:repeat(4,1fr);
        overflow-y: scroll;
    } 
}